/* ECL-MENU, ECL-PAGINATION, ECL-POPOVER */
.ecl-breadcrumb,
.ecl-menu,
.ecl-pagination,
.ecl-popover,
.ecl-site-header__language {
  /* CAUTION: ECL does not give cursor to links without href, but angular does not use hrefs for routing  */
}
.ecl-breadcrumb a:not([href]):not([tabindex]),
.ecl-menu a:not([href]):not([tabindex]),
.ecl-pagination a:not([href]):not([tabindex]),
.ecl-popover a:not([href]):not([tabindex]),
.ecl-site-header__language a:not([href]):not([tabindex]) {
  cursor: pointer;
}

/* ECL-MODAL */
.ecl-modal {
  z-index: 10000;
}

/* ECL-TABS */
.ecl-tabs {
  margin-bottom: 0;
}

/* ECL-BLOCKQUOTE */
.ecl-blockquote__quote {
  border-left: 0;
  padding: 0;
}

/* ECL-MULTISELECT, ECL-MENU */
.ecl-select__multiple .ecl-checkbox__label,
.ecl-select__multiple .ecl-select-multiple-selections-counter span,
.ecl-menu__link {
  box-sizing: unset;
}

/* ECL-ACCORDION - print */
@media print {
  .ecl-accordion__content[hidden] {
    display: block !important;
  }
}
/* ECL-EXPANDABLE - print */
@media print {
  .ecl-expandable__content[hidden] {
    display: block !important;
  }
}
/* ECL-TABLE - mobile view */
@media screen and (max-width: 995px) {
  .ecl-table:not(.ecl-table--simple) .ecl-table__cell:before {
    box-sizing: unset;
  }
}
@media screen and (max-width: 995px) {
  .ecl-table:not(.ecl-table--simple) .ecl-table__cell:after {
    box-sizing: unset;
  }
}
/* ECL-MEGA-MENU */
.ecl-icon {
  box-sizing: unset;
}

.ecl-mega-menu__wrapper {
  box-sizing: unset;
}

/* GENERAL */
.eui-ecl-u-cursor-pointer {
  cursor: pointer;
}

/* ECL-GALLERY */
.ecl-gallery__description {
  box-sizing: unset;
}

/* ECL-INPAGE-NAVIGATION */
.ecl-container h2 :focus, :focus-visible {
  outline: auto;
}

